import { Component, OnInit, ViewChild} from '@angular/core';

import { ShoppingListService } from '../shopping-list.service';
import { Ingredient } from 'src/app/Shared/ingredient.model';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css']
})
export class ShoppingEditComponent implements OnInit {
@ViewChild('f') form:NgForm;
editedItemIndex : number;
editedItem : Ingredient;
editMode = false;
  constructor(private shoppinglistService:ShoppingListService) { }

  OnSubmit(form: NgForm)
  {
    const value = form.value;
    const newIngridient = new Ingredient(value.name,value.amount);
    if(this.editMode === true)
    {
      this.shoppinglistService.UpdateIngridient(this.editedItemIndex,newIngridient);      
      this.editMode = false;    
    }
    else
    {
      this.shoppinglistService.AddIngridient(newIngridient);
    }
    this.form.reset();
  }

  OnDelete()
  {
    this.shoppinglistService.DeleteIngredient(this.editedItemIndex);
    this.OnClear();
  }
  OnClear()
  {
    this.form.reset();
    this.editMode = false;
  }
  ngOnInit() {
    this.shoppinglistService.IngridientEdit.subscribe(
      (index:number) => 
      {
        this.editMode = true;
        this.editedItemIndex = index;
        this.editedItem = this.shoppinglistService.GetIngridient(this.editedItemIndex);
       
        this.form.setValue({
          name: this.editedItem.name,
          amount:this.editedItem.amount
        });
       
      }
    )
  }

}
