import { Ingredient } from '../Shared/ingredient.model';
import { Subject } from 'rxjs/Subject';


export class ShoppingListService{


    ingredients :Ingredient[] =[new Ingredient("Apples",5),new Ingredient("Tomatoes",3),new Ingredient("Peppers",6)];

    IngridientAdded = new Subject<Ingredient[]>();
    IngridientEdit = new Subject<number>();

    GetIngridient(index: number)
    {
      if(index >= 0)
      {
       return this.ingredients[index];
      }
    }
    SetIngridients(ingridients:Ingredient[])
    {
      this.ingredients = ingridients;
    }
    UpdateIngridient(index:number,ingridientToUpdate:Ingredient)
    {
      if(index > -1)
      {
        this.ingredients[index] = ingridientToUpdate;
      }
      this.IngridientAdded.next(this.GetIngrediants());
    }
    AddIngridient(ingridient: Ingredient)
    {
      let index = this.checkIngridientExist(ingridient);
      if(index != -1)
      {
        this.ingredients[index].amount += ingridient.amount;
      }
      else
      {
        this.ingredients.push(ingridient);
      }
      this.IngridientAdded.next(this.GetIngrediants());
    }

    AddIngridients(ingridients:Ingredient[])
    {
      for(let i = 0; i < ingridients.length;i++)
      {
        let index = this.checkIngridientExist(ingridients[i]);
        if(index != -1)
        {
          this.ingredients[index].amount += ingridients[i].amount;
        }
        else
        {
          this.ingredients.push(ingridients[i]);
        }
      }
      this.IngridientAdded.next(this.GetIngrediants());
    }
    GetIngrediants()
    {
        return this.ingredients.slice();
    }

   private checkIngridientExist(ingridient:Ingredient)
    {
       for(let i = 0; i < this.ingredients.length;i++)
       {
         if(this.ingredients[i].name == ingridient.name)
            {
              return i;
            }
       }
       return -1;
    }

    DeleteIngredient(index:number)
    {
      if(index > -1)
      {
        this.ingredients.splice(index,1);
      }
      this.IngridientAdded.next(this.GetIngrediants());
    }
}