import { Component } from '@angular/core';
import {  DataAcessService } from '../Shared/data-acess.service';
import { AuthService } from '../auth/auth.service';
import { Router } from '@angular/router';

@Component({
    templateUrl:'./header.component.html',
    selector:'app-header'
})
export class HeaderComponent{   

    constructor(private dataAcess:DataAcessService,private authService:AuthService,private router:Router)
    {
        
    }
    OnSave()
    {
        this.dataAcess.SaveRecepies().subscribe(
            (res) => console.log(res)
        );
        this.dataAcess.SaveShoppingList().subscribe(
            (res) => console.log(res)
        );
    } 
    
    OnFetch()
    {
     this.dataAcess.GetRecepies();  
    this.dataAcess.GetShoppingList();
    }

    OnLogOut()
    {
        this.authService.LogOut();
        this.router.navigate(['/']);
    }

    isLoggedIn()
    {
        return this.authService.IsLoggedIn();
    }
}