import { Injectable } from '@angular/core';
import { RecipesService } from '../recipes/recipes.service';
import { ShoppingListService } from '../shopping-list/shopping-list.service';
import { Recipe } from '../recipes/recipe.model';
import { Ingredient } from './ingredient.model';
import { AuthService } from '../auth/auth.service';
import { HttpClient, HttpParams } from '@angular/common/http';

@Injectable()
export class DataAcessService{

    constructor(private recepieService : RecipesService,private shoppingListService:ShoppingListService,private http:HttpClient,private authService:AuthService)
    {

    }

    SaveRecepies()
    {
        let token = this.authService.getToken();
        return this.http.put('https://projectapp-abafb.firebaseio.com/recepies.json',
        this.recepieService.GetRecipes(),{
            params:new HttpParams().set('auth',token)
        })
    }

    GetRecepies()
    {      
        let token = this.authService.getToken();  
    return this.http.get<Recipe[]>('https://projectapp-abafb.firebaseio.com/recepies.json',{params:new HttpParams().set('auth',token)})
      .map(
        (recepies) => {
            for(let recepie of recepies)
            {
                if(!recepie['ingredients'])
                {
                    recepie['ingredients'] = [];
                }
            }
           return recepies;
        }
      ).subscribe(
          (recipes:Recipe[]) => {
            this.recepieService.SetRecepies(recipes);
            this.recepieService.recepieChanged.next(this.recepieService.GetRecipes())
          }
      )
    }

    SaveShoppingList()
    {
        let token = this.authService.getToken();
        return this.http.put('https://projectapp-abafb.firebaseio.com/shoppinglist.json?auth=' + token,
        this.shoppingListService.GetIngrediants())
    }

    GetShoppingList()
    {
        let token = this.authService.getToken();  
        return this.http.get<Ingredient[]>('https://projectapp-abafb.firebaseio.com/shoppinglist.json?auth=' + token)
        .subscribe(
            (ingredients:Ingredient[]) => {
              this.shoppingListService.SetIngridients(ingredients);
              this.shoppingListService.IngridientAdded.next(this.shoppingListService.GetIngrediants());
            }
        )
    }
}